.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav {
  align-items: center;
  margin-left: auto;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
  display: flex;
  position: relative;
}

@media (width: 1024px) {
  .nav .header {
    width: 1024px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link:hover, .nav .is-active {
  color: #00acdc;
}

.nav .is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}

.page {
  font-family: Roboto, serif;
}

.page__body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: transform .3s linear;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  align-items: flex-end;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__photo {
  justify-content: center;
  padding-bottom: 40px;
  display: flex;
}

.card__photo-container {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__container-reyting {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__container-reyting .card__stars {
  display: flex;
}

.card__container-reyting .card__stars .star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__container-reyting .card__stars .star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__container-reyting .card__stars .star:last-child {
  margin-right: 0;
}

.card__container-reyting .card__reviews {
  margin: 0;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button a {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color, color, border, all .3s linear;
  display: flex;
}

.card__button a:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
